<template>
    <div class="login">
      <div class="box"></div>
			
			<form class="denglu">
				<h3 class="active"><span>登陆</span><router-link class="cha" to="/home"></router-link></h3>
		    <input type="text" id="" v-model="username" class="tt shu" placeholder="手机号"/>		
		    <input type="password" v-model="password" name="" class="pwd shu" placeholder="密码"/>	
        <p>还没有账号？点击此处立即<router-link to="/reg">注册</router-link>	</p>
        <p><span>{{mess}}</span></p>
        
			  <!-- <p>还没有账号？点击此处立即<a href="#">注册</a></p> -->
		    <input type="submit" class="sub" name="" value="提 交" @click="login"/>		
			</form>	
    </div>
</template>
<script>
export default {
  data(){
    return {
      username:'',
      password:'',
      mess:""
    }
  },
  methods:{
    login(){
      axios({
        url:'/api/login',
        method:'post',
        data:{username:this.username,password:this.password,dbName:"home"}
      }).then(
        res => {
          // console.log('1',res);
          if(res.data.err===0){
            this.$router.push('/user')
            // console.log(1)
          }else{
            this.mess = res.data.msg
          }
        }
      )
    }
  }
}
</script>

<style>
.box{position: fixed;z-index:5 ;
background:rgba(0,0,0,.3);
top: 0;bottom: 0;
left: 0;right: 0;margin: auto;}
	.denglu{position: fixed;z-index:99 ;background: #fff;
	top:0;bottom: 0;left: 0;right: 0;margin: auto;
				height: 4.51rem;width:5.01rem;
				flex-direction: column;
			border:.01rem solid #919191;text-align: center;}
			
			.denglu h3{width: 100%;
				font:.28rem/.6rem " ";text-align:center;
			position: relative;
			border-bottom:.01rem solid #ebebeb;color: #EBEBEB;
			}
.denglu span{display:block;}
  .denglu .cha{width: 0.3rem;
  height: 0.3rem;display: block;
  position: absolute;
  right: 0.5rem;
  top:0.15rem;
    background:url(../assets/img/guanbi.png) no-repeat  center ;}  
		.denglu h3.active{color: #0DC441;}
	
	.denglu	input{width: 4.15rem;}	
	.denglu	input.shu{margin:.12rem 0 .16rem ;
			color: #aaa;
				border: 0;border-bottom: .01rem solid #b2b2b2;}
		.denglu	input.tt{font:.24rem/.65rem " ";}
	.denglu	input.pwd{font:.24rem/.62rem " ";}	
		.denglu	p{font: .24rem/.50rem " ";color: #999;height: .50rem;}
		.denglu	p a{color: #a3e9b7;text-decoration:underline;}
		.denglu	p span{color: red}
	.denglu	.sub{height: .6rem;margin-top: .15rem;
		border: 0;background: #0dc441;font-size: .24rem;
		color: #fff;border-radius: .05rem;
		}

</style>






