<template>
    <div class="nav">
        <Findnav></Findnav>
        <Findserch></Findserch>
        <Findlist :list="list" dataName="home" ></Findlist>
    </div>
</template>
<script>
import Findnav from "../components/Findnav";
import Findlist from "../components/Findlist";
import Findserch from "../components/Findserch"
export default {
    data(){
        return {
        list:[]
        }
    },
    components:{Findnav,Findserch,Findlist},
    mounted(){
        axios({
            url:'/api/column',
            params:{dbName:"home"}
        }).then(
            res=>{this.list = res.data.data;console.log(this.list)}
        )
    }
}
</script>



