<template>
    <div class="floow">
        <Followlist :list="list" dataName="follow"></Followlist>
    </div>
    
</template>
<script>
import Followlist from "../components/Followlist"
export default {
    data(){
        return{
            list:[]
        }
    },
    components:{Followlist},
    mounted(){
        axios({
            url:"api/follow",
            params:{dbName:"home"}
        }).then(
            res=>{this.list = res.data.data;}
        )
    }


}
</script>
