import Vue from "vue";
import VueRouter from "vue-router"; 
Vue.use(VueRouter);

import Find from '../pages/Find.vue';
import Home from '../pages/Home.vue';
import Hot from '../pages/Hot.vue';
import User from '../pages/User.vue';
import Follow from '../pages/Follow.vue';
import Login from "../pages/Login.vue";
import Reg from "../pages/Reg.vue";
import Detail from '@/pages/Detail';
import Scene from "../components/scene";
import Life from "../components/life";
import Beauty from "../components/Beauty";
let routes=[
     
    
     {path:"/home",component:Home,
     redirect: '/home/hot',
     
          children:[
               {path:"/home/hot",component:Hot,
                    redirect: '/home/hot/scene',
                    children:[
                        { path:"/home/hot/scene",component:Scene},
                        { path:"/home/hot/life",component:Life},
                        { path:"/home/hot/Beauty",component:Beauty},
                    ]
               },
               {path:"/home/follow",component:Follow},
               // {
               //      path: '/dec/decUse/onlineRate ', // 默认的子路由
               //      name: 'onlineRate',
               //      component: OnlineRate,
               //      meta: {
               //        title: '设备在线率'
               //      }
               //    }
          ]
     },
     {path:'/detail/:id',component:Detail},
     {path:"/find",component:Find},
     {path:"/user",component:User},
     {path:"/login",component:Login},
     {path:"/reg",component:Reg},
     {path:"/",redirect:"/home/hot"},
];

let router =new VueRouter({routes});
export default router;





