<template>
    <div class="scene">
        <HotList :list="list" dataName="scene"></HotList>
    </div>
</template>


<script>

import HotList from "../components/HotList";
export default {
    data(){
        return{
            list:[]
        }
    },
    components:{HotList},
    mounted(){
        axios({
            url:"api/scene"
        }).then(
            res=>{this.list=res.data.data;}
        )
    }
}
</script>
