<template>
    <div class="Beauty">
        <HotList :list="list" dataName="Beauty"></HotList>
    </div>
</template>


<script>

import HotList from "../components/HotList";
export default {
    data(){
        return{
            list:[]
        }
    },
    components:{HotList},
    mounted(){
        axios({
            url:"api/beauty"
        }).then(
            res=>{this.list=res.data.data;}
        )
    }

}
</script>
